/*主体背景色*/
#index > .content {
  background-color: #fdfefe;
}
/*大尺寸屏幕界面优化*/
@media screen and (min-width: 614px) {
  .view>.content,
  .view>header {
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.3);
    width: 24rem;
    margin: 0 auto;
  }
}
@media screen and (min-width: 614px) {
  .ware-wrap {
    width: 26% !important;
    margin: 3% 0 3% 5% !important;
  }
  #Tel {
    width: 24rem !important;
  }
  .row {
    margin-left: 0 !important;
  }
  #tab3 .row {
    width: 98% !important;
  }
}
/*动画样式*/
.fade-transition {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  opacity: 1;
}
.fade-enter,
.fade-leave {
  opacity: 0;
}
/*通用样式*/
ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}
.bar-sec {
  margin-top: 0.4rem;
  background-color: #1a7ace;
}
.bar-sec ~ .content {
  top: 3rem;
}
header.bar {
  background-color: #1a7ace;
}
header.bar .title,
header .icon {
  color: #fdfefe !important;
}
.dark-blue,
.modal-button {
  color: #1975c8 !important;
}
@font-face {
  font-family: 'webFont';
  src: url("./assets/fonts/webFont.eot");
  src: url("./assets/fonts/webFont.eot?#iefix") format('embedded-opentype'), url("./assets/fonts/webFont.woff") format('woff'), url("./assets/fonts/webFont.ttf") format('truetype'), url("./assets/fonts/webFont.svg") format('svg'), url("./assets/fonts/webFont.otf") format('otf');
  font-weight: normal;
  font-style: normal;
}
.webFont {
  font-family: 'webFont';
}


